import Vue from 'vue';
import { mapActions } from 'vuex';
export default Vue.extend({
    props: ['list'],
    data() {
        return {
            isAddCard: false,
            inputTitle: '',
        };
    },
    methods: {
        ...mapActions(['CREATE_CARD']),
        onAddCard() {
            this.isAddCard = true;
            this.$nextTick(() => {
                const inputTitle = this.$refs.inputTitle;
                inputTitle.focus();
            });
        },
        onClose() {
            this.isAddCard = false;
            this.inputTitle = '';
        },
        onSubmit() {
            const title = this.inputTitle.trim();
            if (title === '')
                return;
            const listId = this.list.id;
            const lastCard = this.list.cards[this.list.cards.length - 1];
            const pos = lastCard ? lastCard.pos * 2 : 65535;
            this.CREATE_CARD({ title, listId, pos }).then(() => this.onClose());
        },
    },
});
