import Vue from 'vue';
import { mapActions } from 'vuex';
import AddCard from '@/components/AddCard.vue';
import CardItem from '@/components/CardItem.vue';
export default Vue.extend({
    props: ['data'],
    components: {
        AddCard,
        CardItem,
    },
    data() {
        return {
            isEditList: false,
            inputTitle: '',
        };
    },
    methods: {
        ...mapActions(['UPDATE_LIST', 'DELETE_LIST']),
        onEditList() {
            this.isEditList = true;
            this.$nextTick(() => {
                const inputTitle = this.$refs.inputTitle;
                inputTitle.focus();
            });
        },
        onDeleteList() {
            if (!confirm(`Are you sure to delete this list '${this.data.title}'`))
                return;
            const lid = this.data.id;
            this.DELETE_LIST(lid);
        },
        onClose() {
            this.isEditList = false;
            this.inputTitle = '';
        },
        onSubmit() {
            const title = this.inputTitle.trim();
            if (title === '')
                return this.onClose();
            const lid = this.data.id;
            const data = {
                title,
            };
            this.UPDATE_LIST({ lid, data }).then(() => this.onClose());
        },
    },
});
