import Vue from 'vue';
export default Vue.extend({
    props: ['data'],
    computed: {
        bid() {
            return this.$store.state.board.id;
        },
        cid() {
            return this.data.id;
        },
    },
    methods: {
        onRemove() {
            if (!confirm(`Are you sure to delete this card '${this.data.title}'`))
                return;
            this.$store.dispatch('DELETE_CARD', this.cid.toString());
        },
    },
});
