import Vue from 'vue';
import { mapActions } from 'vuex';
import dragger from '@/utils/dragger';
import ListItem from '@/components/ListItem.vue';
import AddList from '@/components/AddList.vue';
import BoardSetting from '@/components/BoardSetting.vue';
export default Vue.extend({
    components: {
        ListItem,
        AddList,
        BoardSetting,
    },
    data() {
        return {
            isBoardSetting: false,
            isInputTitle: false,
            inputTitle: '',
            cDragger: null,
            lDragger: null,
        };
    },
    computed: {
        board() {
            return this.$store.state.board;
        },
        bid() {
            return Number(this.$route.params.bid);
        },
    },
    methods: {
        ...mapActions([
            'FETCH_BOARD',
            'UPDATE_BOARD',
            'UPDATE_LIST',
            'UPDATE_CARD',
        ]),
        onBoardSetting(toggle) {
            this.isBoardSetting = toggle;
        },
        onInputTitle() {
            this.isInputTitle = true;
            this.$nextTick(() => {
                const inputTitle = this.$refs.inputTitle;
                inputTitle.focus();
            });
        },
        onSubmit() {
            const inputTitle = this.inputTitle.trim();
            if (inputTitle === '')
                return (this.isInputTitle = false);
            if (inputTitle === this.board.title)
                return (this.isInputTitle = false);
            const bid = this.bid;
            const data = {
                title: inputTitle,
            };
            this.UPDATE_BOARD({ bid, data }).then(() => (this.isInputTitle = false));
        },
        setCardDragger() {
            if (this.cDragger)
                this.cDragger.destroy();
            const containers = Array.from(this.$el.querySelectorAll('.card-wrapper'));
            this.cDragger = dragger.init(containers);
            if (!this.cDragger)
                return;
            this.cDragger.on('drop', (el, wrapper) => {
                const cid = el.dataset.cardId * 1;
                const data = {
                    pos: 63353,
                };
                const { prev, next } = dragger.sibling({
                    el,
                    candidates: Array.from(wrapper.querySelectorAll('.card-item-wrapper')),
                    type: 'card',
                });
                if (!prev && next)
                    data.pos = next.pos / 2;
                else if (!next && prev)
                    data.pos = prev.pos * 2;
                else if (next && prev)
                    data.pos = (prev.pos + next.pos) * 2;
                this.$store.dispatch('UPDATE_CARD', { cid, data });
            });
        },
        setListDragger() {
            if (this.lDragger)
                this.lDragger.destroy();
            const containers = Array.from(this.$el.querySelectorAll('.list-wrapper'));
            const options = {
                invalid: (el) => !el.classList.contains('list-item'),
            };
            this.lDragger = dragger.init(containers, options);
            if (!this.lDragger)
                return;
            this.lDragger.on('drop', (el, wrapper) => {
                const lid = el.dataset.listId * 1;
                const data = {
                    pos: 63353,
                };
                const { prev, next } = dragger.sibling({
                    el,
                    candidates: Array.from(wrapper.querySelectorAll('.list-item')),
                    type: 'list',
                });
                if (!prev && next)
                    data.pos = next.pos / 2;
                else if (!next && prev)
                    data.pos = prev.pos * 2;
                else if (next && prev)
                    data.pos = (prev.pos + next.pos) * 2;
                this.UPDATE_LIST({ lid, data });
            });
        },
    },
    created() {
        const bid = this.bid;
        this.$store.dispatch('FETCH_BOARD', bid).then(() => {
            this.inputTitle = this.board.title;
            this.$store.commit('SET_THEME', this.board.bgColor);
        });
    },
    updated() {
        this.setCardDragger();
        this.setListDragger();
    },
});
