import Vue from 'vue';
import { mapState, mapActions } from 'vuex';
export default Vue.extend({
    data() {
        return {
            isAddList: false,
            inputTitle: '',
        };
    },
    computed: {
        ...mapState(['board']),
    },
    methods: {
        ...mapActions(['CREATE_LIST']),
        onAddList() {
            this.isAddList = true;
            this.$nextTick(() => {
                const inputTitle = this.$refs.inputTitle;
                inputTitle.focus();
            });
        },
        onClose() {
            this.isAddList = false;
            this.inputTitle = '';
        },
        onSubmit() {
            const title = this.inputTitle.trim();
            if (title === '')
                return this.onClose();
            const boardId = this.board.id;
            const lastList = this.board.lists[this.board.lists.length - 1];
            const pos = lastList ? lastList.pos * 2 : 65535;
            this.CREATE_LIST({ title, boardId, pos }).then(() => this.onClose());
        },
    },
});
